<template>
  <!-- Modal -->
  <div class="modal fade" id="add-prod-modal" tabindex="-1" aria-labelledby="add-prod-modalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="add-prod-modalLabel">Adauga un produs</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" ref="Close"></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label for="search-prod" class="form-label">Nume</label>
            <input type="text" id="search-prod" class="form-control form-control" placeholder="Text..." v-model="newProd.Name" />
          </div>
          <div class="d-flex">
            <div class="dropdown w-100">
              <button class="btn btn-outline-dark dropdown-toggle border text-start w-100" type="button" id="dropdown-categories" data-bs-toggle="dropdown" aria-expanded="false">
                <span v-if="Object.keys(selectedCategory).length === 0"> Alege categoria... </span>
                <span v-else>
                  {{ selectedCategory.Name }}
                </span>
              </button>
              <ul class="dropdown-menu" aria-labelledby="dropdown-categories">
                <li v-for="(categ, index) in categoriesProp" v-on:click="selectedCategory = categ" :key="index">
                  <span class="dropdown-item">
                    {{ categ.Name }}
                  </span>
                </li>
              </ul>
            </div>
            <i
              v-if="Object.keys(selectedCategory).length != 0"
              v-on:click="selectedCategory = {}"
              class="border border-danger fa-2x fa-solid fa-xmark pe-2 ps-2 rounded-3 text-danger ms-2"
              style="padding-top: 2px"
            ></i>
          </div>
        </div>
        <div class="modal-footer justify-content-between">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button type="button" class="btn btn-primary" v-on:click="AddProdToJsonFile()">Save changes</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddProductModal",
  //   props: {
  //     categoriesProp: {
  //       type: Array,
  //       default: [],
  //     },
  //   },
  props: ["categoriesProp"],
  emits: ["getProducts"],
  data() {
    return {
      newProd: {
        Name: null,
        CategoryId: 0,
        CategoryName: null,
        IsPurchased: false,
      },
      selectedCategory: {},
    };
  },
  methods: {
    AddProdToJsonFile: function () {
      this.newProd.CategoryId = this.selectedCategory.Id;
      this.newProd.CategoryName = this.selectedCategory.Name;
      this.$axios
        .post("/products", this.newProd)
        .then((response) => {
          console.log(response.status);
          this.newProd = {
            Name: null,
            CategoryId: 0,
            CategoryName: null,
            IsPurchased: false
          };
          this.selectedCategory = {};
          this.$emit("getProducts");
          this.$refs.Close.click();
        })
        .catch((err) => console.log(err.message));
    },
  },
  created() {},
};
</script>
