<template>
  <div>
    <div class="loader-position" v-if="isLoading">
      <span class="loader" style="top: 45%"></span>
    </div>
    <div class="container text-start pe-3 ps-3 pb-4">
      <h1 class="text-center mt-4">Quick-Shopping</h1>
      <hr />
      <div class="row mb-4 text-center">
        <div class="col-4 mb-3" v-for="(categ, index) in categories" :key="index">
          <button type="button" class="btn w-100" v-bind:class="[selectedCategory == categ ? 'bg-primary text-white' : 'btn-outline-primary']" v-on:click="ToggleSelectedCategory(categ)">
            <span>
              {{ categ.Name }}
            </span>
          </button>
        </div>
      </div>
      <div class="row align-items-end">
        <div class="col">
          <label for="search-prod" class="form-label">Cauta un produs...</label>
          <input type="text" id="search-prod" class="form-control form-control form-control-lg" placeholder="Text..." v-model="searchProducts" />
        </div>
        <div class="col-auto">
          <i class="fa-regular fa-square-plus fa-3x text-success pointer" data-bs-toggle="modal" data-bs-target="#add-prod-modal"></i>
        </div>
        <!-- <div class="col-12 mt-5" v-for="(categ, index) in categories" :key="index">
        <div class="card">
          <img :src="require(`../assets/images/${categ.Image}.webp`)" class="card-image" />
          <div class="card-body">
            <h4 class="card-title text-center">{{ categ.Name }}</h4>
          </div>
        </div>
      </div> -->
        <div class="col-12 mt-5" v-for="(prod, index) in filteredProducts" :key="index">
          <div class="card" v-bind:class="{ 'bg-light-success': prod.IsPurchased }">
            <div class="align-items-center card-header d-flex justify-content-between">
              <i class="fa-solid fa-xmark fa-2x text-danger pointer" v-on:click="DeleteProduct(prod.id)"></i>
              <i v-if="!prod.IsPurchased" class="fa-solid fa-check fa-2x text-success pointer" v-on:click="TogglePurchaseProduct(prod)"></i>
              <i v-else class="fa-solid fa-rotate-left fa-2x text-warning pointer" v-on:click="TogglePurchaseProduct(prod)"></i>
            </div>
            <div class="card-body">
              <h4 class="card-title text-center">{{ prod.Name }}</h4>
            </div>
            <div class="card-footer">
              <h6 class="text-center text-uppercase mb-0">{{ prod.CategoryName }}</h6>
            </div>
          </div>
        </div>
      </div>
      <img v-if="filteredProducts.length == 0" :src="require(`../assets/images/result_not_found.jpg`)" class="w-100 mt-5" />

      <AddProductModal :categoriesProp="categories" @getProducts="GetProducts()" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import AddProductModal from "@/components/AddProductModal.vue";
export default {
  name: "HomeView",
  components: { AddProductModal },
  data() {
    return {
      isLoading: false,
      products: [],
      searchProducts: null,
      selectedCategory: {},
      categories: [
        {
          Id: 0,
          Name: "Legume",
          Image: "vegetables",
        },
        {
          Id: 1,
          Name: "Fructe",
          Image: "fruits",
        },
        {
          Id: 2,
          Name: "Carne",
          Image: "meat",
        },
        {
          Id: 3,
          Name: "Lactate",
          Image: "dairy_products",
        },
        {
          Id: 4,
          Name: "Dulciuri",
          Image: "sweets",
        },
        {
          Id: 5,
          Name: "Bautura",
          Image: "drinks",
        },
        {
          Id: 6,
          Name: "Snack-uri",
          Image: "drinks",
        },
        {
          Id: 7,
          Name: "Uz casnic",
          Image: "drinks",
        },
        {
          Id: 8,
          Name: "Loki",
          Image: "drinks",
        },
      ],
    };
  },
  computed: {
    filteredProducts() {
      if (this.searchProducts) {
        return this.products.filter((item) => {
          return this.searchProducts
            .toLowerCase()
            .split(" ")
            .every((v) => item.Name.toLowerCase().includes(v));
        });
      }
      if (Object.keys(this.selectedCategory).length != 0) {
        return this.products.filter((x) => x.CategoryId == this.selectedCategory.Id);
      }
      return this.products;
    },
  },
  methods: {
    TogglePurchaseProduct: function (prod) {
      this.isLoading = true;
      prod.IsPurchased = !prod.IsPurchased;
      this.$axios
        .put(`/products/${prod.id}`, prod)
        .then((response) => {
          console.log(response);
          this.GetProducts();
          this.isLoading = false;
        })
        .catch((e) => {
          console.error(e);
          this.isLoading = false;
        });
    },
    ToggleSelectedCategory: function (category) {
      if (this.selectedCategory == category) this.selectedCategory = {};
      else this.selectedCategory = category;
    },
    GetProducts: function () {
      this.isLoading = true;
      this.$axios
        .get("/products")
        .then((response) => {
          this.products = response.data;
          this.products.sort((x) => (x.IsPurchased ? 1 : -1));
          this.isLoading = false;
        })
        .catch((e) => {
          console.error(e);
          this.isLoading = false;
        });
    },
    DeleteProduct: function (id) {
      console.log(id);
      this.isLoading = true;
      this.$axios
        .delete("/products/" + id)
        .then((response) => {
          console.log(response.status);
          this.GetProducts();
          this.isLoading = false;
        })
        .catch((e) => {
          console.error(e);
          this.isLoading = false;
        });
    },
  },
  created() {
    this.GetProducts();
  },
};
</script>
